*,
*::after,
*::before{
    margin:0;
    padding:0;
    box-sizing: border-box;
}

html{
    font-size: 62.5%;


    @media only screen and (max-width: 37.5em) {font-size: 56.25%; }; 
    @media only screen and (max-width: 75em) { font-size: 50%;  }; 
    @media only screen and (min-width: 112.5em) {  font-size: 75%; }; 
   
       
}

body{
    font-family: 'Fira Sans Condensed', sans-serif;
    font-size: 1.6rem;
    line-height: 1.7;

}



